

body {
 
  margin: 0;
  font-family: Inter, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    system-ui;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #E7F6FF;
  
}

* {
  font-family: Inter;
}




code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.wrapper {
  
  background: #ffffff;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
  border-radius: 20px;
  max-width: 1080px;
  margin: 85px  auto;
   
  }

header {
  border-bottom: 1px solid #EAEAEA ;


}

h1 {
  font-style: inter;
}



.card-button {
font-size:larger;
width: 32px;
height: 32px;
background: #FFFFFF;
border: 1px solid #F2F2F2;
border-radius: 8px;
cursor: pointer;
color:#F2F2F2;

}

.draver {
  
    padding-right: 17px;

  max-height: 200vh;
  overflow: hidden

  
}

.basket {
  margin-top: 150px;
  text-align: center;
  justify-content: center;

  b{
    font-weight: 600;
    font-size: 22px;
    color: #000000;
  }

  p{
    font-size: 16px;
    line-height: 24px;
    text-align: center;

    color: #000000;

    opacity: 0.4;

  }


   .return{
    cursor: pointer;
    opacity: 0.9;
    transition: opacity 0.2s ease-in-out;

    &:hover{
      opacity: 1;
    }
  }
}

.heart{
  position: relative;
  bottom: -40px;
}  

.search-block {
position: relative;
border: 1px solid #F3F3F3;
border-radius: 10px;
 
  input{
    border: 0;
    padding: 5px;
    width: 200px;
  }

  .clearBtn{
    position: absolute;
    margin: 9px;
    right: 0;
    width: 18px;
    height: 18px;
    

  }

}

.slider {  
  margin: 40px auto;
  width: 960px;
  height: 300px;
  background: #F4EFE9;
  border-radius: 20px;

.frog {
  height: 100%
}
}

.content {
  margin: 60px;
}



.cartTotalBlock {
  div {
    flex: 1;
    height: 1px;
    border-bottom: 1px dashed rgb(187, 187, 187);
  }

  .order {
    display: block;
    margin: 25px auto;
    cursor: pointer;
    opacity: 0.9;
    transition: opacity 0.2s ease-in-out;

    &:hover{
      opacity: 1;
    }
  }
}




.cartItem{

margin-bottom: 20px;
margin-top: 30px;
padding: 15px;
background: #FFFFFF;
border: 1px solid #F3F3F3;
border-radius: 20px;

  
  
  height: 119px;

  p {
    font-size: 14px;

  }

  b{
    font-size: 13px;
  }

  .removeBtn {

    cursor: pointer;
    opacity: 0.6;
    transition: opacity 0.12s ease-in-out;

    &:hover{
      opacity: 1;;
    }

  }
}